import Vue from 'vue'
import Vuex from 'vuex'
import axios from './mixins/axios'


Vue.use(Vuex)
const store = new Vuex.Store({
    state: {
        BILLERS: null,
        MENUS: {
            movies: '',
            airlines: '',
            foods: '',
            events: ''
        },
        USER: null,
    },
    mutations: {
        // retrieve_user(state, payload) {
        //     return state.USER = payload
        // },
        retrieve_billers(state, payload) {
            return state.BILLERS = payload
        },
        retrieve_menus(state, payload) {
            return state.MENUS = payload
        },
        retrieve_movies(state, payload) {
            return state.MENUS.movies = payload
        },
        retrieve_events(state, payload) {
            return state.MENUS.events = payload
        },
        retrieve_airlines(state, payload) {
            return state.MENUS.airlines = payload
        },
        retrieve_foods(state, payload) {
            return state.MENUS.foods = payload
        },
        update_user (state, payload) {
            return state.USER = payload
        }
    },
    actions: {
        // retrieve_user(context, payload) {
        //     // eslint-disable-next-line
        //     console.log('from Store', payload)
        //     return context.commit('retrieve_user', payload)
        //     // context.commit('retrieve_user', payload)
        //     // context.commit('retrieve_user', resp.data)
        //     // // Fetch all movies 
        //     // return new Promise((resolve) => {
        //     //     axios.get('/getname')
        //     //         .then((resp) => {
        //     //             resolve()
        //     //         })
        //     //         .catch((err) => { throw new Error(err) })
        //     // });
        // },
        retrieve_billers(context) {
            // Fetch all billers 

            function getMenu(allData, menu) {
                let data = allData.filter((item) => {
                    // eslint-disable-next-line
                    // console.log(item.category.id)
                    return item.category.description.toLowerCase() === menu.toLowerCase() ? item : ''
                })
                return data
            }
            return new Promise((resolve) => {
                axios.get('/getbillers')
                    .then((resp) => {
                        let movies = getMenu(resp.data, 'movies')
                        // eslint-disable-next-line
                        // console.log(movies)
                        context.commit('retrieve_movies', movies)
                        let airlines = getMenu(resp.data, 'airline')
                        context.commit('retrieve_airlines', airlines)
                        let events = getMenu(resp.data, 'events')
                        context.commit('retrieve_events', events)
                        let foods = getMenu(resp.data, 'foods')
                        context.commit('retrieve_foods', foods)
                        resolve(context.commit('retrieve_billers', resp.data))
                    })
                    .catch((err) => { throw new Error(err) })
            });
        },
        update_user (context, payload) {
            return context.commit('update_user', payload)
        }
    }

})

export default store