import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ScrollAnimation from './directives/scrollanimation'
// import jQuery from 'jquery'
import 'bootstrap/dist/css/bootstrap.min.css'
// global.jQuery = jQuery
let bootstrap = require('bootstrap')
import axios from './mixins/axios'
import VueAxios from 'vue-axios'
import VueCookie from 'vue-cookies'
import { library } from '@fortawesome/fontawesome-svg-core'
import {
  faUserSecret, faAngleDown, faPlane, faPlayCircle,
  faPlaneDeparture, faCalendar, faCalendarAlt, faMale,
  faChild, faBaby, faSearch, faEnvelope, faPhone,
  faStar, faCaretDown, faAngleDoubleRight, faAngleRight,
  faAngleLeft 
}
  from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import VueAWN from "vue-awesome-notifications"
import AOS from "aos";
import "aos/dist/aos.css";

// Your Notificaton/Alert custom options
let options = {
  position: 'top-right'
}
// var eventHub = new Vue()

window.$ = require('jquery')
window.JQuery = require('jquery')

Vue.use(VueAWN, options)

Vue.directive('scrollanimation', ScrollAnimation)
library.add(faUserSecret, faAngleDown, faPlane,
  faPlayCircle, faPlaneDeparture, faCalendar, faCalendarAlt, faMale, faChild,
  faBaby, faSearch, faEnvelope, faPhone, faStar, faCaretDown, faAngleDoubleRight, faAngleLeft, faAngleRight)

Vue.use(VueCookie)
Vue.use(VueAxios, axios)
Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  bootstrap,
  created() {
    AOS.init({ disable: "phone" });
  },
  render: h => h(App)
}).$mount('#app')
